.news {
  min-height: 100vh;
  background-color: purple;
  padding-left: 50px;
  padding-right: 50px;
  color: white;
  display: flex;
  flex-direction: column;
}

.news-list {
  flex: 1;
  display: flex;
  flex-direction: row;
}

.news-item {
  flex: 1;
  height: auto;
  width: 100%;
}

.spacing {
  margin-right: 20px;
}

.see-more {
  margin-top: 20px;
  margin-bottom: 40px;
  text-align: center;
}

.see-more:hover {
  color: rgba(255, 255, 255, 0.5);
  cursor: pointer;
}
